import { useEffect } from 'react';

function useMediaSession(youtubePlayerRef, handleNext, handlePrev, handlePlay, handlePause) {
  useEffect(() => {
    if ('mediaSession' in navigator) {
      navigator.mediaSession.metadata = new window.MediaMetadata({
        title: 'Your Video Title', // This can be dynamic if you wish
        artist: 'Artist Name',      // Optional
        album: 'Album Name',        // Optional
        artwork: [
          { src: 'https://example.com/artwork.jpg', sizes: '96x96', type: 'image/png' },
          { src: 'https://example.com/artwork.jpg', sizes: '128x128', type: 'image/png' },
        ]
      });

      // Set up action handlers
      navigator.mediaSession.setActionHandler('play', () => handlePlay());
      navigator.mediaSession.setActionHandler('pause', () => handlePause());
      navigator.mediaSession.setActionHandler('nexttrack', () => handleNext());
      navigator.mediaSession.setActionHandler('previoustrack', () => handlePrev());
    }

    return () => {
      // Clear handlers when the component unmounts
      if ('mediaSession' in navigator) {
        navigator.mediaSession.setActionHandler('play', null);
        navigator.mediaSession.setActionHandler('pause', null);
        navigator.mediaSession.setActionHandler('nexttrack', null);
        navigator.mediaSession.setActionHandler('previoustrack', null);
      }
    };
  }, [handleNext, handlePrev, handlePlay, handlePause]); 
}

export default useMediaSession;
